.card {
    width: 300px;
    height: 400px;
    background-color: var(--bg-color-secondary);
    border-radius: 0 15px;
    position: relative;
}

.card_description {
    padding: 10px;
    text-align: justify;
    font-size: 15pt;
}

.card_img {
    border-radius: 0 15px 0 15px;
    overflow: hidden;
    width: 100%;
}



.link {
    background-color: var(--roxo);
    height: 50px;
    color: var(--branco);
    padding: 3pt 16pt;
    text-align: right;
    border-radius: 0 15px;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.link a {
    color: var(--branco);
}